import { KeyboardEvent, useState } from "react";
import { Input, Select } from "antd";
import { SendOutlined } from "@ant-design/icons";
import { nanoid } from "nanoid";
import { Todo } from "@/types";
import { options } from "@/data";
import { useTodoListDispatch } from "../context";
import { ActionType } from "../context/reducer";

export default function TodoInput() {
	// 
	const [inputValue, setInputValue] = useState<string>("");
	const [selectedValue, setSelectedValue] = useState<string[]>(["important"]);

	// 
	const dispatch = useTodoListDispatch();

	// 
	function handleKeyUp(event: KeyboardEvent<HTMLInputElement>) {
		if (event.key !== "Enter") return;
		submit();
	}

	// 
	function submit() {
		if (inputValue.trim() === "") return;

		// 
		const todo: Todo = {
			id: nanoid(),
			content: inputValue,
			date: Date.now().toString(),
			type: selectedValue,
			completed: false
		};

		// 
		dispatch({ type: ActionType.ADD_TODO, payload: todo });

		// 
		setInputValue("");
	}

	return (
		<div className="flex items-center">
			<Select
				className="w-[190px] mr-3"
				mode="multiple"
				maxTagCount={1}
				placeholder=""
				value={selectedValue}
				options={options}
				onChange={(val) => { setSelectedValue(val); }}
			/>
			<Input
				addonAfter={<SendOutlined onClick={submit}/>}
				maxLength={200}
				value={inputValue}
				onKeyUp={handleKeyUp}
				onChange={(e) => { setInputValue(e.target.value); }}
			/>
		</div>
	);
}
